.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 100;
}
.header__logo{
    height: 25px;
    object-fit: contain;
    margin-left: 20px;
}
.header__input{
    display: flex;
    align-items: center;
    width: 40%;
    border: 1px solid lightgray;
}
.header__icons{
    display: flex;
    align-items: center;
}
.header__icon{
    margin-right: 5px;

}
.header__input > input{
    flex: 1;
    border: none;
} 
.header__inputButton{
    width: 50px !important;
    background-color: #fafafa;
    border: 1px solid lightgray;
    color: grey;
}

